.clip-outer {
	position: relative;
	z-index: -1;
}
.clip1 {
	-webkit-clip-path: polygon(0 90%, 100% 0, 100% 100%, 0% 100%);
	        clip-path: polygon(0 90%, 100% 0, 100% 100%, 0% 100%);
	background-color: rgb(177, 237, 252);
	width: 100vw;
	height: 150px;
	position: absolute;
	top: 0;
}
.clip2 {
	-webkit-clip-path: polygon(0 0, 100% 90%, 100% 100%, 0% 100%);
	        clip-path: polygon(0 0, 100% 90%, 100% 100%, 0% 100%);
	background-color: rgb(173, 170, 175);
	width: 100vw;
	height: 150px;
	position: absolute;
	top: 0;
}

.landing-img-outer {
	margin: auto;
	position: relative;
	display: -webkit-flex;
	display: flex;
}
.landing {
	color: #fff;
	/* background: linear-gradient(to right, #303d46, #1dbfaf); */
}

.landing-img-outer img {
	width: 40%;
}
.landing-img-outer .r1 {
	width: 10%;
	position: absolute;
	bottom: 5%;
}
.landing-img-outer .r2 {
	position: absolute;
	z-index: -1;
	right: -11.25vw;
	top: 7.931vw;
	width: 10.7492vw;
}
.home {
	overflow: hidden;
}

.rb-editor {
	font-family: "Roboto", sans-serif;
	color: white;
}
.rb-editor-container {
	width: 210mm;
	padding: 0px;
}
.rb-editor-page {
	width: 210mm;
	height: 297mm;
	background-color: white;
}
.rb-editor-setting {
	position: fixed;
	top: 50px;
	width: 400px;
	z-index: 1100;
	background-color: #425061;
	border-radius: 50px;
}
.rb-editor-setting .col-3 {
	cursor: pointer;
}

.rb-editor-setting svg {
	width: 1.5rem;
}
.rb-divider {
	background-color: #313b47 !important;
	height: 2px !important;
	opacity: 1;
}
.list-group-item {
	border: none;
}

.rb-main {
	/* background-color: #313b47; */
}

.box {
	height: 350px;
	border-radius: 5px;
	box-shadow: 1px 1px 5px #1b2129;
	margin: 1em;
}
.beta-content {
	background-color: #425061;
}

